@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}:focus{outline:0}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}input[type=search]::-webkit-search-cancel-button,input[type=search]::-webkit-search-decoration,input[type=search]::-webkit-search-results-button,input[type=search]::-webkit-search-results-decoration{-webkit-appearance:none;-moz-appearance:none}input[type=search]{-webkit-appearance:none;-moz-appearance:none;box-sizing:content-box}textarea{overflow:auto;vertical-align:top;resize:vertical}audio,canvas,video{display:inline-block;*display:inline;*zoom:1;max-width:100%}audio:not([controls]){display:none;height:0}[hidden]{display:none}html{font-size:100%;-webkit-text-size-adjust:100%;-ms-text-size-adjust:100%}a:focus{outline:thin dotted}a:active,a:hover{outline:0}img{border:0;-ms-interpolation-mode:bicubic}figure{margin:0}form{margin:0}fieldset{border:1px solid #c0c0c0;margin:0 2px;padding:0.35em 0.625em 0.75em}legend{border:0;padding:0;white-space:normal;*margin-left:-7px}button,input,select,textarea{font-size:100%;margin:0;vertical-align:baseline;*vertical-align:middle}button,input{line-height:normal}button,select{text-transform:none}button,html input[type="button"],input[type="reset"],input[type="submit"]{-webkit-appearance:button;cursor:pointer;*overflow:visible}button[disabled],html input[disabled]{cursor:default}input[type="checkbox"],input[type="radio"]{box-sizing:border-box;padding:0;*height:13px;*width:13px}input[type="search"]{-webkit-appearance:textfield;box-sizing:content-box}input[type="search"]::-webkit-search-cancel-button,input[type="search"]::-webkit-search-decoration{-webkit-appearance:none}button::-moz-focus-inner,input::-moz-focus-inner{border:0;padding:0}textarea{overflow:auto;vertical-align:top}table{border-collapse:collapse;border-spacing:0}html,button,input,select,textarea{color:#222}::selection{background:#b3d4fc;text-shadow:none}img{vertical-align:middle}fieldset{border:0;margin:0;padding:0}textarea{resize:vertical}.chromeframe{margin:0.2em 0;background:#ccc;color:#000;padding:0.2em 0}::-webkit-scrollbar{width:6px}::-webkit-scrollbar-thumb{border-radius:5px;height:127px;background-color:blue}::-webkit-scrollbar-track{width:0}.App{font-family:'Montserrat', sans-serif;font-size:20px;line-height:24px}.container{display:flex}.content{flex-grow:1;padding:0 32px;width:100%}.title{font-weight:700;font-size:26px;line-height:32px;margin-bottom:16px;margin-top:32px}.subtitle{margin-bottom:24px}

.header{display:flex;justify-content:space-between;align-items:center;position:sticky;top:0;z-index:100;background-color:#4F27BF;padding:16px 36px 16px 32px;color:#fff;font-weight:700}.header__link{text-decoration:none;color:#fff}.header__logo{display:flex;align-items:center}.header__logo-img{height:48px;width:48px;margin-right:24px}@media (max-width: 600px){.header{justify-content:center}}

.log-info{color:#fff;font-weight:700}.log-info span{position:relative}.log-info span::before{position:absolute;content:'';background-image:url(/xlab/static/media/login.cdac2bb2.svg);left:-64px;top:-14px;width:48px;height:48px}.log-info.mobile{display:none}@media (max-width: 600px){.log-info.desctop{display:none}.log-info.mobile{display:block;text-align:center;padding:30px 0;background-color:#4F27BF}}

.nav{border-right:rgba(255,255,255,0.05);box-shadow:0px 0px 5px rgba(2,0,96,0.25);height:100%;position:sticky;left:0;top:0;bottom:0;z-index:1;color:#1C1C1E;padding-top:24px}.nav__title{font-weight:700;margin-bottom:8px;padding-left:32px}.nav__menu{width:300px}.nav__hamburger{display:none}@media (max-width: 768px){.nav{max-height:100%}.nav__title{display:none}.nav__menu{width:auto}.nav__hamburger{display:block;cursor:pointer;margin-bottom:32px}.nav__hamburger .bar{display:block;width:32px;height:3px;margin:5px auto;transition:all 0.3s ease-in-out;background-color:#101010}.nav.toggle{position:absolute;left:0;bottom:0;right:0;background-color:#fff;z-index:10000}.nav.toggle .nav__menu{width:100vw;display:flex;flex-direction:column;align-items:center}.nav.toggle .nav__hamburger{position:relative;left:40%}.nav.toggle .nav__hamburger .bar:nth-child(2){opacity:0}.nav.toggle .nav__hamburger .bar:nth-child(1){-webkit-transform:translateY(8px) rotate(45deg);transform:translateY(8px) rotate(45deg)}.nav.toggle .nav__hamburger .bar:nth-child(3){-webkit-transform:translateY(-8px) rotate(-45deg);transform:translateY(-8px) rotate(-45deg)}}

.menu__item-img{margin-right:16px;width:32px;height:32px}.menu__item-link{text-decoration:none;display:flex;align-items:center;cursor:pointer;padding:24px 0 24px 32px;color:#1C1C1E;font-weight:600}.menu__item-link.nested{padding-left:80px}.menu__item-link:hover{border:2px solid #4F27BF}.menu__item-link.active{border:none;border-right:6px solid #4F27BF;border-radius:5px;outline:none}.menu__item-link.active:focus{outline:none}.menu__item-link.active:hover{border:2px solid #4F27BF;border-right:6px solid #4F27BF}@media (max-width: 768px){.menu__item-text{display:none}.menu__item-link{padding:24px}.menu__item-link.nested{padding:24px}.menu__item-img{margin-right:0}.menu__item.toggle{width:100%}.menu__item.toggle .menu__item-link{justify-content:center;align-items:flex-start}.menu__item.toggle .menu__item-link.active{border:6px solid #4F27BF}.menu__item.toggle .menu__item-text{display:block;text-align:center}.menu__item.toggle .menu__item-img{margin-right:16px}}

.main{white-space:pre-wrap;overflow-wrap:break-word;padding:0 32px;overflow:auto;overflow-x:hidden}.main__content-paragraph{margin-bottom:30px}

.search-address__help-info{margin-bottom:24px}

.search{display:flex;justify-content:space-around;width:100%;margin-bottom:32px}.search__btn-wrapper{margin-left:24px}@media (max-width: 984px){.search{flex-wrap:wrap}}

.input{border:3px solid #4F27BF;width:100%;border-radius:10px;padding:23px 24px;font-size:20px;line-height:24px;color:#1C1C1E;font-weight:500}.input::-webkit-input-placeholder{font-size:20px;line-height:24px;font-weight:500;color:#6C6C6C}.input:-ms-input-placeholder{font-size:20px;line-height:24px;font-weight:500;color:#6C6C6C}.input::placeholder{font-size:20px;line-height:24px;font-weight:500;color:#6C6C6C}

.search__btn{background-color:#4F27BF;color:#fff;padding:19px 40px;border-radius:10px;display:flex}.search__btn-text{font-weight:700;font-size:26px;line-height:32px;margin-left:16px}@media (max-width: 984px){.search__btn{margin:20px 0}}

.address-list{border:rgba(255,255,255,0.05);box-shadow:0px 0px 5px rgba(2,0,96,0.25);border-radius:10px;padding:24px;padding-bottom:0;margin-bottom:32px}

.address-item{border-top:1px solid #D7D7D7;padding:16px 0}

